<template>
  <div class="info">
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      sort-by="viewCount"
      update: sort-desc
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title><span style="color: #fff; text-shadow: 0 0 5px #000"><b>DANH SÁCH THỐNG KÊ</b></span></v-toolbar-title>
          <v-spacer></v-spacer>
          <div class="search-box">
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Tìm kiếm"
              single-line
              hide-details
            ></v-text-field>
          </div>
          <!-- <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                Thêm kênh
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        v-model="editedItem.link"
                        label="Link kênh"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close"> Hủy </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Lưu </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog> -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="headline"
                >Are you sure you want to delete this item?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>
      <!-- <template v-slot:no-data>
      <v-btn color="primary" @click="initialize"> Reset </v-btn>
    </template> -->
    </v-data-table>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      search: "",
      dialog: false,
      dialogDelete: false,
      headers: [
        { text: "Tài khoản", value: "account", sortable: false },
        {
          text: "Tên kênh",
          align: "start",
          sortable: false,
          value: "name",
        },
        { text: "Đăng ký", value: "subscriberCount" },
        { text: "Lượt xem", value: "viewCount" },
        { text: "Số video", value: "videoCount" },
        // { text: "Actions", value: "actions", sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        link: "",
      },
      defaultItem: {
        link: "",
      },
      accountList: [],
      gmailList: [],
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Thêm kênh" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.desserts = this.accountList;
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.close();
    },

    formatNumber(value) {
      return String(value)
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
  },
  mounted() {
    axios
      .get(
        "https://sheets.googleapis.com/v4/spreadsheets/1zgv5ogk5reWsfrAToiecQuhfaKM6nwyLSHp0eqgSBb8/values/Worksheet!1:60?key=AIzaSyAAUda1-y7m8HYDOrDBr_-rqdMtf9TJZRI"
      )
      .then((response) => {
        this.gmailList = response.data.values;
        for (let item of this.gmailList) {
          axios
            .get("https://www.googleapis.com/youtube/v3/channels", {
              params: {
                part: "statistics, snippet",
                key: "AIzaSyAAUda1-y7m8HYDOrDBr_-rqdMtf9TJZRI", //AIzaSyAyNYkNtBMdxgl6M5GIzP9s1LUVJjp2oKk
                id: item[1].slice(item[1].length - 24, item[1].length),
              },
            })
            .then((response) => {
              const profile = response.data.items[0];
              this.accountList.push({
                account: item[0],
                name: profile.snippet.title,
                subscriberCount: this.formatNumber(
                  profile.statistics.subscriberCount
                ),
                videoCount: this.formatNumber(profile.statistics.videoCount),
                viewCount: profile.statistics.viewCount,
              });
            });
        }
      });
  },
};
</script>
<style>
@media (min-width: 480px) {
  .v-sheet {
    background: none !important;
  }
  .info {
    background: linear-gradient(to bottom right, #003366 0%, #ff0066 100%);
    height: 100vh;
    overflow: hidden;
  }
  .v-data-table {
    background: rgba(255, 255, 255, 0.4) !important;
    margin: 0 auto;
    height: 100vh;
  }
  .v-data-footer {
    background: none !important;
  }
  .theme--light.v-text-field:not(.v-input--has-state):hover
    > .v-input__control
    > .v-input__slot:before {
    display: none !important;
  }
  .theme--light.v-text-field > .v-input__control > .v-input__slot:before {
    display: none !important;
  }
  tbody tr:hover {
    background: linear-gradient(
      to bottom right,
      #7182a3 0%,
      #e86ba4 100%
    ) !important;
  }
  .search-box {
    margin-right: 20px;
  }
}
@media (max-width: 480px) {
  .v-toolbar__title {
    display: none;
  }
  .spacer {
    display: none;
  }
  .search-box {
    width: 100%;
  }
  .info {
    background: linear-gradient(to bottom right, #003366 0%, #ff0066 100%);
  }
  .v-data-table {
    background: rgba(255, 255, 255, 0.4) !important;
    min-height: 100vh;
  }
  .v-data-footer {
    background: none !important;
  }
}
</style>




